import jsonTaxons from "@/json/taxons.json";
import jsonQuests from "@/json/quests.json";
import jsonTaxonTree from "@/json/taxonTree.json";

export function getGrades(scores, maxScores) {
  let grades = (scores / maxScores) * 5;
  let fgrades = Math.floor(grades);
  let cgrades = Math.ceil(grades);
  console.log(grades);
  console.log(fgrades);
  console.log(cgrades);
  if (grades - cgrades == 0) return grades > 1 ? grades : 1;
  if (grades - fgrades < 0.4) return `${grades > 1 ? fgrades : 1}+`;
  if (grades - fgrades > 0.4) return `${grades > 1 ? cgrades : 1}-`;
  // return scores > 1 ? (scores / maxScores) * 5 : 1;
}

